import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Detail from '@/components/Detail'
import ArtList from '@/components/ArticleList'
import UserProfile  from '@/components/user/Profile'
import UserArticleList from '@/components/user/UserArticle'
import UserGallery  from '@/components/user/UserGallery'
import UserStarList  from '@/components/user/UserStarList'
// import Test from '@/components/scroll/ScrollTest'
import Test from '@/components/test/ScrollDemo'
import Swiper from '@/components/Swiper/SwiperDemo'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',redirect:'/index'
    },
    {
      path:'/index',
      name:'Index',
      component:Index,
      meta:{keepAlive: true}
    },{
      path:'/detail/:id',
      name:'detail',
      component:Detail
    },{
      path:'/article/list',
      name:'articleList',
      component:ArtList
    },
    {
      path:'/user/profile',
      name:'UserProfile',
      component:UserProfile
    },
    {
      path:'/user/article',
      name:'UserArticleList',
      component:UserArticleList,
    },
    {
      path:'/user/gallery',
      name:'UserGallery',
      component:UserGallery
    },
    {
      path:'/user/stars',
      name:'UserStarList',
      component:UserStarList,
    },
    {
      path:'/test',
      name:'Test',
      component:Test,
    },
    {
      path:'/swiper',
      name:'SwiperDemo',
      component:Swiper,
    }
    



  ]
})



