// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import 'vuetify/dist/vuetify.min.css'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import store from './store'
import VueCookies from 'vue-cookies'
import Vuelidate from 'vuelidate'
import AppServerCof from './appServerConfig'
import VueTouch from 'vue-touch'
import Toasted from 'vue-toasted';
import remoteAddress from './properties'
import axios from 'axios'
import {
  Store
} from 'vuex';



// axios.defaults.baseURL="http://server.com/api"  单独部署的app生产环境中替换为这个，和后台一起部署的话就不用了
axios.defaults.baseURL = "http://49.234.192.162:8091"
// axios.defaults.baseURL = "http://127.0.0.1:8091"
Vue.prototype.$axios = axios
Vue.prototype.$remoteProperties = remoteAddress
axios.interceptors.response.use(
  res => {
    if (!res.data.result && res.data.code == 666) {
      console.log("token失败截获")
      window.getVue.$emit("FIX-ACCESSTOKEN")
    }

    return res;
  },
  err => {
    let toast = Vue.toasted.show(err, {
      theme: "bubble",
      position: "top-right",
      duration: 1000
    });
    return Promise.reject(err);
  }
)
axios.interceptors.request.use(
  config => {

    return config;
  },
  err => {
    let toast = Vue.toasted.show(err, {
      theme: "bubble",
      position: "top-right",
      duration: 1000
    });
    return Promise.reject(err);
  }
)
Vue.use(Toasted, {
  singleton: true
})
Vue.use(VueTouch, {
  name: 'v-touch'
})
Vue.use(Vuelidate)
Vue.config.productionTip = false
Vue.use(Vuetify)
Vue.use(VueCookies)

Vue.prototype.checkLogin = (showToast) => {
  // localStorage.setItem()不会把json转为string，存储的时候最好使用JSON.stringefy()将对象转为string存储。
  try {
    let LoginInfo = JSON.parse(localStorage.getItem('geeklemon-login-info'));
    if (Boolean(LoginInfo) && LoginInfo.timestamp) {
      let isLogin = true;
      let {
        user,
        timestamp
      } = LoginInfo;
      let now = new Date().getTime;
      let timeDiff = now - timestamp;
      let day = Math.floor(timeDiff / (24 * 3600 * 1000)); //天数

      if (day > 30) {
        localStorage.setItem('geeklemon-login-info', null);
        isLogin = false;
        msg = "登陆信息过期";
        user = null;
        openId = '';
      }
      return {
        isLogin: isLogin,
        user: user,
        timestamp: timestamp
      };
    } else {
      return {
        isLogin: false,
        user: null,
      };
    }
  } catch (e) {
    return {
      isLogin: false,
      user: null,
    };
  }
}

// document.addEventListener('deviceready', function () {
new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  data() {
    return {
      reTryToken: 0
    }
  },
  template: '<App/>',
  created() {
    this.checklogin(false);
    this.getAppTheme();
    this.checkAccessInfo();
    this.registAppToast();

  },
  methods: {
    async post(url, JSONParam) {
      try {
        let loading = null;
        // if (url.startsWith('/app/api/')) {
        //   loading = this.$toasted.show("loading", {
        //     className: this.$store.state.theme.color,
        //     theme: "bubble",
        //     icon: "hourglass_empty_full",
        //   })
        // }
        let axiosHeaders = this.$store.state.accessInfo;
        let param = null;
        if (JSONParam && typeof (JSONParam) == "object") {
          param = new URLSearchParams();
          for (let key in JSONParam) {
            param.append(key, JSONParam[key]);
          }
        }
        let res = await this.$axios.post(url, param, {
          headers: {
            accessKey: axiosHeaders.accessKey,
            accessToken: axiosHeaders.accessToken
          }
        });
        res = res.data;
        this.$toasted.clear();
        return new Promise((resolve, reject) => {
          if (res.result && res.success) {
            resolve(res)
          } else {
            // reject相当于报错
            this.app_error(res.message)
          }
        })
      } catch (e) {
        this.$toasted.clear();
        this.app_error(e)
      }
    },
    app_error(msg) {
      this.$toasted.global.app_error({
        message: msg
      });
    },
    app_info(msg) {
      this.$toasted.global.app_info({
        message: msg
      });
    },
    app_loading(msg) {
      this.$toasted.global.app_loading({
        message: msg
      });
    },
    app_login(msg) {
      this.$toasted.global.app_login({
        message: msg
      });
    },
    app_warning(msg) {
      this.$toasted.global.app_warning({
        message: msg
      });
    },
    /**
     * 初始化登陆状态，在主组建中初始化就行了，vuex刷新会改变，但是vue之间的跳转是不会改变的。
     */
    checklogin() {
      let loginInfo = this.checkLogin();
      console.log(loginInfo);
      this.$store.commit('changeLoginState', loginInfo);
      console.log("登陆状态：" + loginInfo.isLogin)
    },
    /**
     * 获取浏览器cookie的主题颜色。
     */
    getAppTheme() {
      let theme = this.$cookies.get('geeklemon-app-theme');
      if (Boolean(theme)) {
        let index = 2;
        try {
          index = parseInt(theme);
        } catch (e) {
          index = 2;
        }
        if (index) {
          this.$store.commit('changeTheme', index);
        }
      }
    },
    checkAccessInfo() {
      let info = localStorage.getItem("geeklemon-accessInfo");
      try {
        if (Boolean(info)) {
          let accessInfo = JSON.parse(info);
          let timestamp = accessInfo.timestamp; //
          if (timestamp && accessInfo.accessKey) {
            let now = new Date().getTime();
            let diff = now - timestamp;
            if (diff / (24 * 3600 * 1000) > 1) {
              // 后台控制1天过期
              this.getAccessInfo();
            } else {
              this.$store.commit('valueAccessInfo', accessInfo);
            }
            return;
          } else {
            this.getAccessInfo()
          }
        }
      } catch (e) {
        console.log("checkAccess err:" + e);
      }
      this.getAccessInfo()
    },
    getAccessInfo() {
      // 获取accessKey和accessToken
      let appKeyAndSecret = new URLSearchParams();
      appKeyAndSecret.append("appKey", AppServerCof.appKey);
      appKeyAndSecret.append("appSecret", AppServerCof.appSecret);
      this.$axios.post('/app/accessToken', appKeyAndSecret).then((accessInfo) => {
        if (accessInfo.data.code) {

          let Info = accessInfo.data; //请求过来的数据在data中
          this.$store.commit('valueAccessInfo', Info);
          let accessInfoStr = JSON.stringify(Info);
          localStorage.setItem('geeklemon-accessInfo', accessInfoStr);
        } else {
          this.$toasted.info("token校验失败，稍后重试!")
        }
      });
    },
    registAppToast() {
      this.$toasted.register(
        "app_info",
        payload => {
          if (!payload.message) {
            return "Oops..";
          }
          return payload.message;
        }, {
          className: this.$store.state.theme.color,
          duration: 1000,
          theme: "bubble"
        }
      );
      this.$toasted.register(
        "app_login",
        payload => {
          if (!payload.message) {
            return "未登录";
          }
          return payload.message;
        }, {
          className: this.$store.state.theme.color,
          duration: 1000,
          theme: "outline",
          icon: "lock"
        }
      );
      this.$toasted.register(
        "app_warning",
        payload => {
          if (!payload.message) {
            return "...";
          }
          return payload.message;
        }, {
          className: this.$store.state.theme.color,
          duration: 1000,
          theme: "bubble",
          icon: "error_outline"
        }
      );
      this.$toasted.register("app_loading", "loading...", {
        className: this.$store.state.theme.color,
        duration: 1000 * 10,
        theme: "bubble",
        icon: "hourglass_empty_full"
      });
      this.$toasted.register(
        "app_error",
        payload => {
          if (!payload.message) {
            return "出错了";
          }
          return payload.message;
        }, {
          className: this.$store.state.theme.color,
          duration: 1000,
          theme: "outline",
          icon: "report_problem"
        }
      );
    }

  },
  mounted() {
    window.getVue.$on("FIX-ACCESSTOKEN", () => {
      localStorage.setItem('geeklemon-accessInfo', null);
      this.reTryToken += 1;
      if (this.reTryToken > 5) {
        return
      }
      this.getAccessInfo()

    });
  },
});
// window.navigator.splashscreen.hide()
// }, false);



