<template>
  <div>
    <v-progress-linear v-if="uploadProgress.show" v-model="uploadProgress.progress"></v-progress-linear>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-img :src="userInfo.userlogo" height="300px" @click="changeLogo">
            <v-layout column fill-height>
              <v-card-title>
                <v-btn dark icon @click="back">
                  <v-icon :color="theme.color">chevron_left</v-icon>
                </v-btn>

                <v-spacer></v-spacer>

                <v-btn dark icon class="mr-3" @click="edit">
                  <v-icon :color="theme.color">edit</v-icon>
                </v-btn>

                <v-btn dark icon>
                  <v-icon :color="theme.color">more_vert</v-icon>
                </v-btn>
              </v-card-title>

              <v-spacer></v-spacer>

              <v-card-title class="white--text pl-5 pt-5">
                <div class="display-1 pl-5 pt-5">{{userInfo.nickName}}</div>
              </v-card-title>
            </v-layout>
          </v-img>

          <v-list two-line>
            <v-list-tile>
              <v-list-tile-action>
                <v-icon :color="theme.color">phone</v-icon>
              </v-list-tile-action>

              <v-list-tile-content>
                <v-list-tile-title>(650) 555-1234</v-list-tile-title>
                <v-list-tile-sub-title>Mobile</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>

            <v-divider inset></v-divider>

            <v-list-tile>
              <v-list-tile-action>
                <v-icon :color="theme.color">mail</v-icon>
              </v-list-tile-action>

              <v-list-tile-content>
                <v-list-tile-title>{{userInfo.mail}}</v-list-tile-title>
                <v-list-tile-sub-title>Personal</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>

            <v-divider inset></v-divider>

            <v-list-tile>
              <v-list-tile-action>
                <v-icon :color="theme.color">bookmark</v-icon>
              </v-list-tile-action>

              <v-list-tile-content>{{userInfo.autograph}}</v-list-tile-content>
            </v-list-tile>
            <v-list-tile>
              <v-list-tile-action>
                <v-icon :color="theme.color">calendar_today</v-icon>
              </v-list-tile-action>

              <v-list-tile-content>{{userInfo.age}}</v-list-tile-content>
            </v-list-tile>
            <v-list-tile>
              <v-list-tile-action>
                <v-icon :color="theme.color">accessibility_new</v-icon>
              </v-list-tile-action>

              <v-list-tile-content>{{userInfo.sex}}</v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-card>
      </v-flex>
    </v-layout>
    <!-- 修改头像 -->
    <v-layout row justify-center>
      <v-dialog v-model="uploadLogo" persistent max-width="600px">
        <v-card>
          <v-img :src="upload.userlogo" height="300px" @click="closeUpload">
            <v-layout column fill-height>
              <v-card-title>
                <v-btn dark icon @click="closeUpload">
                  <v-icon :color="theme.color">chevron_left</v-icon>
                </v-btn>

                <v-spacer></v-spacer>

                <v-btn dark icon class="mr-3" @click="uploadClick">
                  <v-icon :color="theme.color">edit</v-icon>
                </v-btn>
              </v-card-title>
            </v-layout>
          </v-img>
          <input
            hidden
            type="file"
            id="uploadClick"
            ref="uploadClick"
            @change="changeimg"
            accept="image/*"
            capture="camera"
          />
          <v-card-actions v-show="upload.submit">
            <v-spacer></v-spacer>
            <v-btn :color="theme.color" @click="startUpload" flat>
              <v-icon>backup</v-icon>更改
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
    <!-- 修改mudal -->
    <v-layout row justify-center>
      <v-dialog v-model="editDialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">修改</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field
                    v-model="user.nickName"
                    prepend-icon="account_circle"
                    :counter="10"
                    label="昵称"
                    required
                    clearable
                    :rules="validate.nameRules"
                  ></v-text-field>
                </v-flex>

                <v-flex xs12>
                  <v-text-field
                    v-model="user.age"
                    prepend-icon="lock"
                    label="年龄"
                    type="number"
                    required
                    clearable
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    v-model="user.mail"
                    prepend-icon="mail"
                    label="邮箱"
                    type="mail"
                    required
                    :rules="validate.mailRules"
                    clearable
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-select prepend-icon="mail" :items="['男','女']" v-model="user.sex" label="性别"></v-select>
                </v-flex>
              </v-layout>
            </v-container>
            <!-- <small v-show="loginErrShow">error message</small> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="editDialog = false">Close</v-btn>
            <v-btn color="blue darken-1" @click="editUser" flat>提交</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>
<script>
export default {
  name: "userProfile",
  data() {
    return {
      valueDeterminate: 60,
      user: {},
      userInfo: {},
      inputText: { value: "", type: "" },
      theme: this.$store.state.theme,
      editDialog: false,
      uploadLogo: false,
      upload: { userlogo: "", submit: false },
      uploadProgress: { show: false, progress: 1 },
      file: null,
      validate: {
        nameRules: [
          v => !!v || "昵称不能空",
          v => (v && v.length < 20) || "昵称长度不能大于20"
        ],
        mailRules: [
          v =>
            /^[a-z\d]+(\.[a-z\d]+)*@([\da-z](-[\da-z])?)+(\.{1,2}[a-z]+)+$/.test(
              v
            ) || "请输入邮箱地址",
          v => !v
        ]
      }
    };
  },
  methods: {
    closeUpload(e) {
      e.stopPropagation();
      this.uploadLogo = false;
      this.upload.submit = false;
      let file = document.getElementById("uploadClick");
      file.outerHTML = file.outerHTML;
    },

    uploadClick(e) {
      e.stopPropagation();
      let uploadbtn = this.$refs.uploadClick;
      uploadbtn.click();
    },
    startUpload() {
      // 上传
      this.uploadLogo = false; //关闭上传头像的窗口
      this.uploadProgress.show = true;
      let formData = new FormData();
      formData.append("file", this.file);
      formData.append("userOpenId", this.userInfo.openid);
      let axiosHeaders = this.$store.state.accessInfo;
      let config = {
        //添加请求头
        headers: {
          "Content-Type": "multipart/form-data",
          accessKey: axiosHeaders.accessKey,
          accessToken: axiosHeaders.accessToken
        },
        //添加上传进度监听事件
        onUploadProgress: e => {
          let completeProgress = ((e.loaded / e.total) * 100) | 0;
          this.uploadProgress.progress = completeProgress;

          if (completeProgress == 100) {
            this.uploadProgress.show = false;
          }
          // console.log(this.uploadProgress);
        }
      };
      let url = this.$remoteProperties.remoteAddress;
      this.$axios
        .post(url, formData, config)
        .then(res => {
          let result = res.data;
          this.userInfo.userlogo =this.$remoteProperties.remoteAddress+"/images/userlogo/" + result.body;
        })
        .catch(e => {
          console.log(e);
        });
    },
    async changeimg(e) {
      // 图片选择事件
      let file = e.target.files[0];
      this.file = file;
      if (!file.type.startsWith("image")) {
        this.$root.app_warning("必须是图片！");
        return;
      }
      let imageSize = parseInt(file.size / 1000); //转为kb单位
      if (imageSize > 1000 * 10) {
        // 大于10M
        this.$root.app_warning("图片不要超过10M");
        return;
      }

      if (window.FileReader) {
        let fr = new FileReader();
        let that = this;
        fr.onloadend = e => {
          that.upload.userlogo = e.target.result;
          that.upload.submit = true;
        };
        fr.readAsDataURL(file);
      }
    },
    changeLogo() {
      // 弹出修改头像窗口
      this.upload.userlogo = this.userInfo.userlogo;
      this.uploadLogo = true;
      // 修改头像
    },
    back(e) {
      e.stopPropagation();
      this.$router.back();
    },
    edit(e) {
      e.stopPropagation();
      this.user = JSON.parse(JSON.stringify(this.userInfo));
      this.editDialog = !this.editDialog;
    },
    async editUser() {
      let mail = this.user.mail;
      let name = this.user.nickName;
      if (JSON.stringify(this.userInfo) == JSON.stringify(this.user)) {
        this.$root.app_info("没有改变信息");
        this.editDialog = !this.editDialog;
        return;
      }
      if (name.trim().length == 0) {
        this.$root.app_warning("昵称不能空");
        return;
      }
      if (name != this.userInfo.nickName && name.trim().length > 20) {
        this.$root.app_warning("昵称过长");
        return;
      }
      if (
        mail.length > 1 &&
        mail != this.userInfo.mail &&
        !/^[a-z\d]+(\.[a-z\d]+)*@([\da-z](-[\da-z])?)+(\.{1,2}[a-z]+)+$/.test(
          mail
        )
      ) {
        this.$root.app_warning("邮箱验证不通过");
        return;
      }

      this.editDialog = false;
      this.$root.app_loading("后台更改中。。");
      let params = {
        userOpenId: this.userInfo.openid,
        nickName: this.user.nickName,
        userAge: this.user.age,
        userEmail: this.user.mail,
        userAutographContent: this.user.autograph
      };
      let result = await this.$root.post("/app/api/user/update", params);

      if (result.body) {
        this.$root.app_info("更改成功");
      }
    },
    isObjectValueEqual(a, b) {
      //取对象a和b的属性名
      var aProps = Object.getOwnPropertyNames(a);
      var bProps = Object.getOwnPropertyNames(b);
      //判断属性名的length是否一致
      if (aProps.length != bProps.length) {
        return false;
      }
      //循环取出属性名，再判断属性值是否一致
      for (var i = 0; i < aProps.length; i++) {
        var propName = aProps[i];
        if (a[propName] !== b[propName]) {
          return false;
        }
      }
      return true;
    },

    getUserProfile() {
      this.$root
        .post("/app/api/user/info", {
          userOpenId: this.user.userOpenId
        })
        .then(result => {
          if (result.body) {
            let info = result.body;
            this.userInfo = info;
          }
        });
    }
  },
  mounted() {
    this.user.userOpenId = this.$route.params.userOpenId || 3;
    this.getUserProfile();
  }
};
</script>



