
<template>
  <v-navigation-drawer id="appDrawer" :mini-variant.sync="mini" fixed dakr app v-model="drawer">
    <v-toolbar :color="theme.color" dark>
      <v-toolbar-title class="ml-0 pl-3">
        <span class="hidden-sm-and-down white--text">geeklemon</span>
      </v-toolbar-title>
    </v-toolbar>
    <v-img @click="gotoUserDetail" :aspect-ratio="16/9" src="../../static/material.webp">
      <v-layout pa-2 column fill-height class="lightbox white--text">
        <v-spacer></v-spacer>
        <v-flex shrink v-if="this.$store.state.loginState.isLogin">
          <div class="subheading">{{this.$store.state.loginState.user.nickName}}</div>
          <div class="body-1">{{this.$store.state.loginState.user.mail}}</div>
        </v-flex>
        <v-flex shrink v-if="!this.$store.state.loginState.isLogin">
          <div class="subheading">未登陆</div>
        </v-flex>
      </v-layout>
    </v-img>
    <template>
      <v-list>
        <v-list-tile to="/index">
          <v-list-tile-action>
            <v-icon>home</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>Home</v-list-tile-title>
        </v-list-tile>

        <v-list-tile v-if="!this.$store.state.loginState.isLogin" @click="toogleLoginMudal">
          <v-list-tile-action>
            <v-icon>account_circle</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>登陆</v-list-tile-title>
        </v-list-tile>

        <v-list-group
          v-if="this.$store.state.loginState.isLogin"
          prepend-icon="explore"
          value="true"
        >
          <template v-slot:activator>
            <v-list-tile>
              <v-list-tile-title>用户中心</v-list-tile-title>
            </v-list-tile>
          </template>
          <v-list-tile v-for="(crud, i) in cruds" :key="i" @click="ToUser(crud[2])">
            <v-list-tile-title v-text="crud[0]"></v-list-tile-title>
            <v-list-tile-action>
              <v-icon v-text="crud[1]"></v-icon>
            </v-list-tile-action>
          </v-list-tile>
        </v-list-group>

        <v-list-tile>
          <v-list-tile-action>
            <v-icon>settings</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>Settings</v-list-tile-title>
        </v-list-tile>

        <v-list-tile @click="fixApp">
          <v-list-tile-action>
            <v-icon>build</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>修复</v-list-tile-title>
        </v-list-tile>

        <v-list-tile v-if="this.$store.state.loginState.isLogin" @click="logOut">
          <v-list-tile-action>
            <v-icon>account_circle</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>注销</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </template>

    <!-- login mudal -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">登陆</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field
                    v-model="userName"
                    prepend-icon="account_circle"
                    :counter="10"
                    label="userName"
                    clearable
                    hint="输入登录名"
                  ></v-text-field>
                </v-flex>

                <v-flex xs12>
                  <v-text-field
                    v-model="password"
                    prepend-icon="lock"
                    label="Password"
                    type="password"
                    required
                    clearable
                  ></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
            <small v-show="loginErrShow">error message</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog = false">Close</v-btn>
            <v-btn color="blue darken-1" flat @click="login">login</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </v-navigation-drawer>
</template>
<script>
export default {
  name: "drawer",
  data() {
    return {
      mini: false, //侧边栏最小化
      drawer: false, //
      Index: "/index", //主页跳转
      dialog: false, //登陆模态框
      userName: "", //登录名
      password: "", //登陆密码
      loginErrShow: false,
      loginErrMsg: "", //登陆出错信息
      theme: this.$store.state.theme, //app主题
      admins: [["Management", "people_outline"], ["Settings", "settings"]],
      cruds: [
        ["资料", "view_quilt", "UserProfile"],
        ["文章", "insert_drive_file", "UserArticleList"],
        ["相册", "movie", "UserGallery"],
        ["收藏", "favorite", "UserStarList"]
      ]
    };
  },
  created() {
    window.getVue.$on("DRAWER_TOGGLED", () => {
      this.drawer = !this.drawer;
    });
    window.getVue.$on("LOGIN_SHOW", () => {
      //注册全局事件调起登陆的mudal
      this.toogleLoginMudal();
    });
  },
  methods: {
    logOut() {
      let state = { isLogin: false, user: null, timestamp: "" };
      localStorage.setItem("geeklemon-login-info", JSON.stringify(state));
      this.$store.commit("changeLoginState", state);
      this.$toasted.global.app_info({ message: "退出登陆" });
    },
    toogleLoginMudal() {
      this.dialog = !this.dialog;
    },
    gotoUserDetail(e) {
      e.stopPropagation();
      if (this.$store.state.loginState.isLogin) {
        
      }else{
        this.toogleLoginMudal()
      }
    },
    fixApp() {
      localStorage.setItem("geeklemon-accessInfo", null);
      //  this.$toasted.global.app_info({message:"刷新试试"})
      window.location.reload();
    },
    ToUser(val) {
      let userOpenId = this.$store.state.loginState.user.openid;
      this.$router.push({ name: val, params: { userOpenId: userOpenId } });
    },
    login() {
      if (
        this.userName.trim().length > 0 &&
        this.userName.trim().length < 25 &&
        this.password.trim().length > 0
      ) {
        let axiosHeaders = this.$store.state.accessInfo;
        let req = new URLSearchParams();
        req.append("name", this.userName);
        req.append("password", this.password);
        this.$axios
          .post("/app/api/user/login", req, {
            headers: {
              accessKey: axiosHeaders.accessKey,
              accessToken: axiosHeaders.accessToken
            }
          })
          .then(res => {
            if (res.data.result) {
              if (res.data.success) {
                let userEntity = res.data.body;
                let userInfo = {
                  isLogin: true,
                  user: userEntity,
                  timestamp: new Date().getTime()
                };
                localStorage.setItem(
                  "geeklemon-login-info",
                  JSON.stringify(userInfo)
                );
                this.$store.commit("changeLoginState", userInfo);
                let msg = "欢迎您，" + userEntity.name;
                this.$toasted.global.app_info({ message: msg });
                this.toogleLoginMudal();
              } else {
                this.$toasted.global.app_error({ message: res.data.message });
              }
            } else {
              this.$toasted.global.app_error({ message: "出错了，稍后重试！" });
              this.toogleLoginMudal();
            }
          });
      } else {
        this.$toasted.global.app_warning({ message: "用户名或密码不能为空！" });
      }
    }
  }
};
</script>


