<template>
  <div>
    <v-layout v-if="loadProgress" justify-center style="position:fixed;width:100%;z-index:2">
      <v-progress-circular indeterminate color="red"></v-progress-circular>
    </v-layout>
    <template>
      <v-layout justify-center>
        <v-flex xs12>
          <v-container fluid grid-list-md>
            <v-layout row wrap>
              <v-flex v-for="(card,i) in cards" :key="i" xs6 sm4>
                <v-card @click="showBigImg(i)">
                  <v-img :src="card.comsrc">
                    <v-container fill-height fluid pa-2>
                      <v-layout fill-height>
                        <v-flex xs12 align-end flexbox>
                          <span class="headline white--text" v-text="card.des"></span>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </v-img>
                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
      </v-layout>
    </template>
    <v-layout row justify-center max-width="100%">
      <!-- <v-dialog v-model="bigImg" persistent max-width="600px">
        <v-img :onreadystatechange="imgStateChange" contain :src="bigImgInfo.src" @click="bigImg = false">
          <v-layout column fill-height>
            <v-card-title>
              <v-btn dark icon @click="bigImg = false">
                <v-icon :color="theme.color">chevron_left</v-icon>
              </v-btn>

              <v-spacer></v-spacer>

              <v-btn dark icon class="mr-3">
                <v-icon :color="theme.color">edit</v-icon>
              </v-btn>

              <v-btn dark icon>
                <v-icon :color="theme.color">delete</v-icon>
              </v-btn>
              <v-btn icon>
                <v-icon :color="theme.color">share</v-icon>
              </v-btn>
            </v-card-title>

            <v-spacer></v-spacer>

            <v-card-title class="white--text pl-5 pt-5">
              <div class="display-1 pl-5 pt-5">{{bigImgInfo.des}}</div>
            </v-card-title>
          </v-layout>
        </v-img>
      </v-dialog> -->


      
    </v-layout>
    <v-layout justify-center two-line>
      <v-btn color="success" @click="moreClick" v-if="!loadProgress" flat>{{loadMore}}</v-btn>
    </v-layout>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cards: [],
      bigImg: false,//大图
      bigImgInfo: { des: "", src: "" },
      theme: this.$store.state.theme,
      userOpenId: "",
      pageNum: 0,
      noMoreToast: false,
      YHeight: 0,
      totalPage: 0,
      loadProgress: false
    };
  },
  computed: {
    loadMore() {
      if (this.pageNum < this.totalPage) {
        return "more";
      } else {
        return "全部加载完";
      }
    }
  },
  methods: {
    imgStateChange(e){
      console.log(e)
    },
    moreClick() {
      if (this.pageNum < this.totalPage) {
        this.getList();
      } else {
        if (!this.noMoreToast) {
          this.$root.app_info("没有数据了");
          this.noMoreToast = true;
        }
      }
    },
    showBigImg(index) {
      this.bigImg = true;
  
      this.bigImgInfo = this.cards[index];
      
    },
    async getList() {
      let result = await this.$root.post("/app/api/gallery/userGallery", {
        userOpenId: this.userOpenId,
        pageNum: this.pageNum + 1
      });
      if (result.body) {
        let list = result.body.list;
        for (let index in list) {
          list[index].comsrc = list[index].comsrc;
          list[index].src = list[index].src;
          this.cards.push(list[index]);
        }
        this.totalPage = result.body.pages;
        this.pageNum += 1;
      }
    },
    imageLoadEvent() {
      console.log("tupian");
    }
  },
  created() {
    this.userOpenId = this.$route.params.userOpenId;
    img.onload = this.imageLoadEvent;
  },
  mounted() {
    this.getList();
  }
};
</script>



