<template>
  <div>
    <v-layout v-if="loadProgress" justify-center style="position:fixed;width:100%;z-index:2">
      <v-progress-circular indeterminate color="red"></v-progress-circular>
    </v-layout>
    <v-container class="wrapper">
      <!-- z-index=2能够显示在本页面的最上层但是不会遮挡侧边栏 -->
      <v-card v-show="searchShow" fluid style="position:fixed;width:100%;padding:16px;z-index:2;">
        <v-layout wrap>
          <v-flex xs4 sm2 d-flex>
            <v-select :items="subjects" v-model="searchSubject" label="分类"></v-select>
          </v-flex>
          <v-flex xs8 sm10>
            <v-text-field counter="25" :autofocus="true" v-model="searchKey" @keyup.enter="search"></v-text-field>
          </v-flex>
        </v-layout>
      </v-card>
      <v-container grid-list-xl fluid>
        <v-layout row wrap>
          <scroll
            :data="articles"
            :pulldown="pulldown"
            :pullup="pullup"
            @scrollToEnd="pullupEvent"
            @pulldown="pulldownEvent"
            :BottomLoad="loadProgress"
            @slideToRight = "slideToRight"
          >
            <v-flex v-for="(item) in articles" :key="item.id" xs12 sm4 md4 lg4>
              <v-card @click="getDetail(item.id)">
                <v-layout row>
                  <v-flex xs7>
                    <v-card-title primary-title>
                      <div>
                        <div class="headline">{{item.title}}</div>
                        <div>{{item.author}}</div>
                        <div>{{item.time}}</div>
                      </div>
                    </v-card-title>
                  </v-flex>
                  <v-flex xs5>
                    <v-img :src="item.src" height="125px" contain></v-img>
                  </v-flex>
                </v-layout>
                <v-divider light></v-divider>
                <v-card-actions class="pa-3">
                  {{item.subject}}
                  <v-spacer></v-spacer>
                  <v-icon>star_border</v-icon>
                  <span>{{item.snum}}</span>
                </v-card-actions>
              </v-card>
            </v-flex>
          </scroll>
        </v-layout>
      </v-container>
      <v-layout justify-center two-line>
        <v-btn color="success" @click="moreClick" v-if="!loadProgress" flat>{{loadMore}}</v-btn>
      </v-layout>
    </v-container>

    <v-speed-dial fixed bottom left direction="top" open-on-hover>
      <template v-slot:activator>
        <v-btn :color="theme.color" dark fab small @click="searchShow = !searchShow">
          <v-icon>reorder</v-icon>
          <v-icon>close</v-icon>
        </v-btn>
      </template>
    </v-speed-dial>
  </div>
</template>
<script>
import scroll from "../test/ScrollTmp";
export default {
  components: {
    scroll: scroll
  },
  name: "index",
  data() {
    return {
      pulldown: true,
      pullup: true,

      theme: this.$store.state.theme,
      articles: [],
      searchShow: false,
      searchKey: "",
      searchSubject: "",
      subjects: ["AI", "emotion", "share"],
      YHeight: 0, //上一次滚动的高度

      paramInfo: { pageNum: 0, pageSize: 6, topic: "", authorOpenId: "" },
      totalArticleNum: 0,
      totalPage: 0,

      loadProgress: false
    };
  },
  computed: {
    loadMore() {
      if (this.paramInfo.pageNum < this.totalPage) {
        return "more";
      } else {
        return "全部加载完";
      }
    }
  },
  watch: {
    searchSubject(val, oldVal) {
      this.changeTopic(val);
    }
  },
  methods: {
    pullupEvent() {
      this.moreClick();
    },
    pulldownEvent() {
      this.initPage();
    },
    slideToRight(){
       this.$router.replace({ name: "Index"});
    },
    changeTopic(newTopic) {
      this.paramInfo.topic = newTopic;
      this.initPage();
    },
    search() {
      if (this.searchKey.trim().length > 25) {
        this.$root.app_info("文字超出部分将不会生效！");
        this.searchKey = this.searchKey.trim().substring(0, 25);
      }
      this.initPage(true);
    },
    getDetail(param) {
      this.$router.push({ name: "detail", params: { id: param } });
    },
    /** flag看是不是获得数据再清空内容，避免搜索没有数据又重新加载 */
    getArticleList(URLSearchParams, flag) {
      this.loadProgress = true;
      let axiosHeaders = this.$store.state.accessInfo;
      this.$axios
        .post("/app/api/article/list", URLSearchParams, {
          headers: {
            accessKey: axiosHeaders.accessKey,
            accessToken: axiosHeaders.accessToken
          }
        })
        .then(res => {
          this.loadProgress = false;
          let resultBody = res.data;
          if (resultBody.success) {
            if (resultBody.body.size == 0) {
              // 当前数量为0
              this.toast(this.searchKey + ":没有数据");
              this.searchKey = "";
              return;
            }
            if (flag) {
              this.articles = [];
            }
            let articleList = resultBody.body.list;
            articleList.forEach(element => {
              // 把src换了
              let src = element.src;
              // element.src = "http://43.226.40.196:8080/images/" + src;
              element.src = src;
              this.articles.push(element);
            });

            this.paramInfo.pageNum += 1;
            this.totalPage = resultBody.body.pages;
          } else {
            this.$root.app_info(resultBody.message);
          }
        })
        .catch(e => {
          this.loadProgress = false;
        });
    },
    async getTopics() {
      let data = await this.$root.post("/app/api/article/topics", null);
      if (data.result) {
        this.subjects = data.body;
      }
    },
    moreClick() {
      if (this.paramInfo.pageNum < this.totalPage) {
        let reqParams = this.getReqParams();
        this.getArticleList(reqParams);
      } else {
        this.$root.app_info("没有更多数据了！");
      }
    },
    initPage(lazyEmpty) {
      if (!lazyEmpty) {
        this.articles = [];
      }
      this.paramInfo.pageNum = 0;
      let reqParams = this.getReqParams();
      this.getArticleList(reqParams, lazyEmpty);
    },
    getReqParams() {
      let reqParams = new URLSearchParams();
      let newPageNum = this.paramInfo.pageNum + 1;
      reqParams.append("pageNum", newPageNum);
      reqParams.append("userOpenId", this.paramInfo.authorOpenId);
      reqParams.append("topic", this.paramInfo.topic);
      reqParams.append("keyWord", this.searchKey);
      return reqParams;
    }
  },
  created() {
    this.paramInfo.authorOpenId =
      this.$route.params.userOpenId ||
      this.$store.state.loginState.user.openid ||
      3;
  },
  mounted() {
    this.getTopics();
    this.initPage();
  },
  beforeRouteLeave(to, from, next) {
    if (to.name == "detail") {
      from.meta.keepAlive = true;
    }
    next();
  }
};
</script>


