<template>
  <div>
    <v-container class="wrapper">
      <!-- z-index=2能够显示在本页面的最上层但是不会遮挡侧边栏 -->
      <v-card v-show="searchShow" fluid style="position:fixed;width:100%;padding:16px;z-index:2;">
        <v-layout wrap>
          <v-flex xs4 sm2 d-flex>
            <v-select :items="subjects" v-model="searchSubject" label="分类"></v-select>
          </v-flex>
          <v-flex xs8 sm10>
            <v-text-field counter="25" :autofocus="true" v-model="searchKey" @keyup.enter="search"></v-text-field>
          </v-flex>
        </v-layout>
      </v-card>
      <v-container grid-list-xl fluid>
        <div style="height:60px"></div>
        <v-layout row wrap>
          <v-flex v-for="(item) in articles " :key=item.id xs12 sm4 md4 lg4>
            <!-- <div style="height:80px"></div> -->
            <v-card>
              <div @click="getDetail(item.id)">
                <v-img :src="item.src" aspect-ratio="2.75"></v-img>

                <v-card-title primary-title>
                  <div>
                    <h3 class="headline mb-0">{{item.title}}</h3>
                    <div style="text-overflow:ellipsis;">{{ item.description }}</div>
                  </div>
                </v-card-title>
              </div>
              <v-card-actions>
                <v-btn flat color="orange">{{item.subject}}</v-btn>
                <v-btn flat color="orange">{{item.author}}</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
        <v-snackbar v-model="snackbar.show" multi-line right :timeout="snackbar.timeout" top>
          {{ snackbar.text }}
          <v-btn color="pink" flat @click="snackbar.show = false">Close</v-btn>
        </v-snackbar>
      </v-container>
    </v-container>
        <v-btn :color="theme.color" @click="refresh" bottom left fixed fab small>
          <v-icon>refresh</v-icon>
        </v-btn>
  </div>
</template>
<script>
export default {
  name: "index",
  data() {
    return {
      theme: this.$store.state.theme,
      articles: [],
      searchShow: true,
      searchKey: "",
      searchSubject: "",
      subjects: ["AI", "emotion", "share"],
      YHeight: 0, //上一次滚动的高度

      mode: 1, //1:默认，2：topic模式，3：user模式，4：search模式

      paramInfo: { pageNum: 0, pageSize: 6, topic: "", authorOpenId: "" },
      totalArticleNum: 0,
      totalPage: 0,

      snackbar: { show: false, text: "", timeout: 1000 }
    };
  },
  watch: {
    searchSubject(val, oldVal) {
      this.changeTopic(val);
    }
  },
  methods: {
    changeTopic(newTopic) {
      window.getVue.$emit("CHANGE_TITLE", newTopic);
      this.paramInfo.topic = newTopic;
      this.initPage();
    },
    search() {
      if (this.searchKey.trim().length > 25) {
        this.toast("文字超出部分将不会生效！");
        this.searchKey = this.searchKey.trim().substring(0, 25);
      }
      this.initPage(true);
    },
    getDetail(param) {
      this.$router.push({ name: "detail", params: { id: param } });
    },
    /** flag看是不是获得数据再清空内容，避免搜索没有数据又重新加载 */
    getArticleList(URLSearchParams, flag) {
      let axiosHeaders = this.$store.state.accessInfo;
      this.$axios
        .post("/app/api/article/list", URLSearchParams, {
          headers: {
            accessKey: axiosHeaders.accessKey,
            accessToken: axiosHeaders.accessToken
          }
        })
        .then(res => {
          let resultBody = res.data;
          if (resultBody.success) {
            if (resultBody.body.size == 0) {
              // 当前数量为0
              this.toast(this.searchKey + ":没有数据");
              this.searchKey = "";
              return;
            }
            if (flag) {
              this.articles = [];
            }
            let articleList = resultBody.body.list;
            articleList.forEach(element => {
               this.articles.push(element);
            });

            this.paramInfo.pageNum += 1;
            this.totalPage = resultBody.body.pages;
          } else {
            this.toast(resultBody.message);
          }
        });
    },
    toast(msg) {
      this.snackbar.text = msg;
      this.snackbar.show = true;
    },
    async getTopics() {
      let data = await this.$root.post("/app/api/article/topics", null);
      if (data&&data.result) {
        this.subjects = data.body;
      }
    },
    handleScroll() {
      let oldScroll = 0;
      //document的滚动高度
      let scrollHeight =
        document.body.scrollHeight ||
        documenhandleScrollt.documentElement.scrollHeight;
      // 已滚动高度
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // 可视区高度
      let nowScotop =
        document.documentElement.clientHeight || document.body.clientHeight;
      oldScroll = scrollTop;
      let scroll = scrollTop - this.YHeight;
      this.YHeight = scrollTop;
      if (scroll < 0) {
        // 上滑
        this.searchShow = true;
      } else {
        // 下滑
        this.searchShow = false;
      }
      if (nowScotop + scrollTop >= scrollHeight) {
        // 滑到最下边
        if (this.paramInfo.pageNum < this.totalPage) {
          let reqParams = this.getReqParams();
          this.getArticleList(reqParams);
        } else {
          this.toast("没有更多数据了！");
        }
      }
    },
    refresh() {
      this.searchKey = "";
      this.paramInfo.authorOpenId = "";
      this.paramInfo.topic = "";
      this.getTopics();
      this.initPage();
    },
    initPage(lazyEmpty) {
      if (!lazyEmpty) {
        this.articles = [];
      }
      this.paramInfo.pageNum = 0;
      let reqParams = this.getReqParams();
      this.getArticleList(reqParams, lazyEmpty);
    },
    getReqParams() {
      let reqParams = new URLSearchParams();
      let newPageNum = this.paramInfo.pageNum + 1;
      reqParams.append("pageNum", newPageNum);
      reqParams.append("userOpenId", this.paramInfo.authorOpenId);
      reqParams.append("topic", this.paramInfo.topic);
      reqParams.append("keyWord", this.searchKey);
      return reqParams;
    }
  },
  created() {
    window.getVue.$on("CHANGE_AUTHOR", authorOpenId => {
      this.searchKey = "";
      this.paramInfo.authorOpenId = authorOpenId;
      this.paramInfo.topic = "";
      this.initPage();
    });
  },
  mounted() {
    this.getTopics();
    this.initPage();
  },
  activated() {
    window.addEventListener("scroll", this.handleScroll);
  },
  deactivated() {
    window.removeEventListener("scroll", this.handleScroll);
  }
};
</script>


