<template>
  <div>
    <scroll
      :data="commentList"
      :pulldown="pulldown"
      :pullup="pullup"
      @pullup="pullupEvent"
      @pulldown="pulldownEvent"
    >
      <div>
        <v-layout v-if="loadProgress" justify-center style="position:fixed;width:100%;z-index:2">
          <v-progress-circular indeterminate color="red"></v-progress-circular>
        </v-layout>
        <v-container grid-list-xl fluid id="detail" ref="content">
          <v-layout row wrap></v-layout>
          <div class="text-xs-center">
            <v-icon :color="theme.color">account_circle</v-icon>
            {{articleInfo.author}}
            <v-icon :color="theme.color">alarm</v-icon>
            {{articleInfo.time}}
            <v-icon :color="theme.color">email</v-icon>
            {{articleInfo.subject}}
            <v-icon :color="starColor" @click="starArticle">favorite</v-icon>
            {{articleInfo.snum}}
          </div>
          <v-badge></v-badge>
          <div v-html="content"></div>
          <div>
            <v-btn flat :color="theme.color">
              <v-icon>book</v-icon>评论
            </v-btn>
          </div>
          <div v-if="commentList.length>0">
            <v-card v-for="(item) in commentList" :key="item.id">
              <v-card-title>{{item.user}} 在 {{item.time}} 说:</v-card-title>
              <v-card-text v-html="item.content"></v-card-text>
            </v-card>
          </div>
          <!-- 悬浮菜单 -->
          <v-speed-dial fixed bottom left direction="top" open-on-hover>
            <template v-slot:activator>
              <v-btn :color="theme.color" dark fab small>
                <v-icon>reorder</v-icon>
                <v-icon>close</v-icon>
              </v-btn>
            </template>
            <v-btn fab dark small :color="starColor" @click="starArticle">
              <v-icon>favorite</v-icon>
            </v-btn>
            <v-btn fab dark small :color="theme.color" @click="toggleComment">
              <v-icon>chat</v-icon>
            </v-btn>
            <v-btn fab dark small :color="theme.color">
              <v-icon>call_split</v-icon>
            </v-btn>
          </v-speed-dial>
          <!-- 评论窗 -->
          <v-layout row justify-center>
            <v-dialog v-model="dialog" persistent max-width="600px">
              <v-card>
                <v-card-title>
                  <span class="headline">评论</span>
                </v-card-title>
                <v-card-text>
                  <v-container grid-list-md>
                    <v-layout wrap>
                      <v-textarea
                        v-model="commentContent"
                        counter="100"
                        name="input-7-1"
                        label="comment"
                        hint
                        autofocus
                        clearable
                      ></v-textarea>
                    </v-layout>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn :color="theme.color" flat @click="dialog = false">Close</v-btn>
                  <v-btn :color="theme.color" flat @click="comment">评论</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-layout>
        </v-container>
      </div>
    </scroll>
  </div>
</template>
<script>
import scroll from "./ScrollTmp";

export default {
  components: {
    scroll: scroll,
  },

  name: "detail",
  data() {
    return {
      pulldown: true,
      pullup: true,
      pulldownShow: true,
      pullupShow: true,
      pullupMsg: "more",
      pulldownMsg: "松开加载",
      stared: false, //是否已收藏
      loadProgress: false,
      articleList: [],
      // content:this.$route.params.id,
      content: "",
      articleId: this.$route.params.id,
      articleInfo: {
        title: "",
        subject: "",
        snum: 0,
        authorId: "",
        author: "",
        time: ""
      },
      dialog: false,
      theme: this.$store.state.theme,

      commentContent: "",
      commentPageNum: 0,
      commentTotalPage: 1,
      commentList: [],

      documentWidth: 1,

      commentAllDone: false
    };
  },
  computed: {
    starColor() {
      if (!this.stared) {
        return this.theme.color;
      }
      return "red";
    }
  },
  mounted() {
    let articleId = this.$route.params.id;
    //获取数据给articleList
    this.getArticleDetail();
  },
  created() {
    this.getArticleDetail();
    this.documentWidth = document.body.clientWidth * 0.9;
  },
  beforeDestroy() {
    // 返回之前把标题改了
    window.getVue.$emit("CHANGE_TITLE", "geeklemon");
  },
  methods: {
    pulldownEvent() {
      this.pulldownShow = true;
      setTimeout(() => {
        this.pulldownShow = false;
      }, 1000);
    },
    pullupEvent() {
      console.log("up");
    },
    pageBack() {
      this.$router.back();
    },
    comment() {
      if (!this.$store.state.loginState.isLogin) {
        this.$toasted.global.app_login();
        return;
      }

      if (this.commentContent.trim().length > 0) {
        this.dialog = false;
        let content = this.commentContent.trim();
        if (this.commentContent.trim().length > 100) {
          content = this.commentContent.trim().substring(0, 100);
          this.$toasted.global.app_warning({ message: "超出字数的字将被抛弃" });
        }
        let loading = this.$toasted.global.app_loading();
        this.commentContent = "";
        let axiosHeaders = this.$store.state.accessInfo;
        let params = new URLSearchParams();
        params.append("articleId", this.$route.params.id);
        params.append("userOpenId", this.$store.state.loginState.user.openid);
        params.append("content", content);
        this.$axios
          .post("/app/api/article/comment", params, {
            headers: {
              accessKey: axiosHeaders.accessKey,
              accessToken: axiosHeaders.accessToken
            }
          })
          .then(res => {
            if (res.data.success) {
              this.$toasted.global.app_info({
                message: "超出字数的字将被抛弃"
              });
            } else {
              this.$toasted.global.app_error({ message: "评论出错，稍后再试" });
            }
          });
      } else {
        this.$toasted.global.app_warning({ message: "内容不能空" });
      }
    },
    toggleComment() {
      if (this.$store.state.loginState.isLogin) {
        this.dialog = !this.dialog;
      } else {
        window.getVue.$emit("LOGIN_SHOW");
        let toast = this.$toasted.global.app_login();
      }
    },
    getArticleDetail() {
      this.loadProgress = true;
      let axiosHeaders = this.$store.state.accessInfo;
      let params = new URLSearchParams();
      params.append("id", this.$route.params.id);
      if (this.$store.state.loginState.isLogin) {
        params.append("userOpenId", this.$store.state.loginState.user.openid);
      }
      this.$axios
        .post("/app/api/article/info", params, {
          headers: {
            accessKey: axiosHeaders.accessKey,
            accessToken: axiosHeaders.accessToken
          }
        })
        .then(result => {
          this.loadProgress = false;
          if (!result.data.result) {
            this.$toasted.global.app_error({ message: result.data.message });
            return;
          }
          let {
            title,
            subject,
            snum,
            authorId,
            author,
            time
          } = result.data.body.result;
          this.articleInfo.title = title;
          this.articleInfo.subject = subject;
          this.articleInfo.snum = snum;
          this.articleInfo.authorId = authorId;
          this.articleInfo.author = author;
          this.articleInfo.time = time;

          this.author = author;
          let starStatus = result.data.body.starStatus;
          this.stared = starStatus;
          window.getVue.$emit("CHANGE_TITLE", title);
          this.content = result.data.body.result.content.replace(
            /src=\"\//g,
            'src="http://43.226.40.196:8080/'
          );
        });
    },
    starArticle() {
      if (this.$store.state.loginState.isLogin) {
        let axiosHeaders = this.$store.state.accessInfo;
        let params = new URLSearchParams();
        params.append("userOpenId", this.$store.state.loginState.user.openid);
        params.append("id", this.articleId);
        if (this.stared) {
          if (confirm("取消收藏吗？")) {
            params.append("opt", "unStar");
          } else {
            return;
          }
        } else {
          params.append("opt", "star");
        }
        this.$axios
          .post("/app/api/article/starOpt", params, {
            headers: {
              accessKey: axiosHeaders.accessKey,
              accessToken: axiosHeaders.accessToken
            }
          })
          .then(res => {
            if (res.data.success) {
              this.$toasted.global.app_info({ message: res.data.body });
              this.stared = !this.stared;
            } else {
              this.$toasted.global.app_error({
                message: "操作失败，请稍后重试！"
              });
            }
          });
      } else {
        window.getVue.$emit("LOGIN_SHOW");
        this.$toasted.global.app_login();
      }
    },
    handleScroll() {
      //document的滚动高度
      let scrollHeight = document.body.scrollHeight;
      // 已滚动高度
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // 可视区高度
      let nowScotop =
        document.documentElement.clientHeight || document.body.clientHeight;
      this.YHeight = scrollTop;
      if (nowScotop + scrollTop >= scrollHeight) {
        // 滑到最下边
        if (this.commentPageNum < this.commentTotalPage) {
          let reqParams = new URLSearchParams();
          reqParams.append("articleId", this.articleId);
          reqParams.append("pageNum", this.commentPageNum + 1);
          this.getComments(reqParams);
        } else {
          if (!this.commentAllDone) {
            //避免每次滑到下面都提示，只提示一次
            this.$toasted.global.app_info({ message: "已加载所有评论！" });
            this.commentAllDone = true;
          }
        }
      }
    },
    getComments(URLSearchParams) {
      let axiosHeaders = this.$store.state.accessInfo;
      this.$axios
        .post("/app/api/article/comment/list", URLSearchParams, {
          headers: {
            accessKey: axiosHeaders.accessKey,
            accessToken: axiosHeaders.accessToken
          }
        })
        .then(res => {
          if (res.data.result) {
            this.commentTotalPage = res.data.body.pages;
            let list = res.data.body.list;
            list.forEach(element => {
              this.commentList.push(element);
            });
            this.commentPageNum += 1;
          }
        });
    }
  },
};
</script>



