<template>
  <div class="tabbar">
    <div class="wrapper" ref="wrapper" v-bind:style="wrapStyle">
      <div class="bscroll-container">
        <!-- 刷新提示信息 -->
        <!-- <div class="top-tip">
          <span class="refresh-hook">{{pulldownMsg}}</span>
        </div>-->
        <v-layout justify-center v-if="downProgress">
          <v-progress-circular indeterminate color="red"></v-progress-circular>
        </v-layout>
        <!-- 内容列表 -->
        <slot></slot>
        <!-- 底部提示信息 -->
        <v-layout justify-center v-if="hasMore">
          <v-btn flat>加载更多</v-btn>
        </v-layout>
        <v-layout justify-center v-if="BottomLoad">
          <v-progress-circular indeterminate color="red"></v-progress-circular>
        </v-layout>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import BScroll from "better-scroll";

export default {
  name: "scroll",
  props: {
    /**
     * 1 滚动的时候会派发scroll事件，会截流。
     * 2 滚动的时候实时派发scroll事件，不会截流。
     * 3 除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
     */
    probeType: {
      type: Number,
      default: 1
    },
    /**
     * 点击列表是否派发click事件
     */
    click: {
      type: Boolean,
      default: true
    },
    /**
     * 是否开启横向滚动
     */
    scrollX: {
      type: Boolean,
      default: true
    },
    /**
     * 是否派发滚动事件
     * 默认触发
     */
    listenScroll: {
      type: Boolean,
      default: true
    },
    /**
     * 子组件把item传过来，这样变化的时候刷新
     */
    item: {
      type: Object,
      default: null
    },
    /**
     * 是否派发滚动到底部的事件，用于上拉加载
     */
    pullup: {
      type: Boolean,
      default: false
    },
    /**
     * 是否派发顶部下拉的事件，用于下拉刷新
     */
    pulldown: {
      type: Boolean,
      default: false
    },
    /**
     * 是否派发列表滚动开始的事件
     */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /**
     * 当数据更新后，刷新scroll的延时。
     */
    refreshDelay: {
      type: Number,
      default: 20
    },
    hasMore: {
      type: Boolean,
      default: false
    },
    BottomLoad: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      pullupMsg: "more",
      pulldownMsg: "下拉加载",
      downProgress: false,
      wrapStyle: {
        width: "100%",
        height: (document.documentElement.clientHeight) * 0.8+'px',
        overflow: "hidden",
        position: "relative"
      }
    };
  },
  mounted() {
    // 保证在DOM渲染完毕后初始化better-scroll
    setTimeout(() => {
      this._initScroll();
    }, 20);
  },
  methods: {
    _initScroll() {
      if (!this.$refs.wrapper) {
        return;
      }
      // better-scroll的初始化
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX,
        mouseWheel: true
      });

      // 是否派发滚动事件
      if (this.listenScroll) {
        this.scroll.on("scroll", pos => {
          if (pos.y > 50) {
            this.downProgress = true;
            this.pulldownMsg = "释放立即刷新";
          }
          this.$emit("scroll", pos);
        });
      }

      // 是否派发滚动到底部事件，用于上拉加载
      if (this.pullup) {
        this.scroll.on("scrollEnd", () => {
          this.downProgress = false;
          // 滚动到底部
          if (this.scroll.y <= this.scroll.maxScrollY + 50) {
            this.$emit("scrollToEnd");
          }
        });
      }

      // 是否派发顶部下拉事件，用于下拉刷新
      if (this.pulldown) {
        this.scroll.on("touchEnd", pos => {
          let y = Math.abs(this.scroll.distY);
          let x = this.scroll.distX;
          if (x > 80 && y < 25) {
            this.$emit("slideToRight");
          }

          this.pulldownMsg = "下拉刷新";
          // 下拉动作
          if (pos.y > 50) {
            this.$emit("pulldown");
          }
        });
      }

      // 是否派发列表滚动开始的事件
      if (this.beforeScroll) {
        this.scroll.on("beforeScrollStart", () => {
          this.$emit("beforeScroll");
        });
      }
    },
    disable() {
      // 代理better-scroll的disable方法
      this.scroll && this.scroll.disable();
    },
    enable() {
      // 代理better-scroll的enable方法
      this.scroll && this.scroll.enable();
    },
    refresh() {
      // 代理better-scroll的refresh方法
      this.scroll && this.scroll.refresh();
    },
    scrollTo() {
      // 代理better-scroll的scrollTo方法
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
    },
    scrollToElement() {
      // 代理better-scroll的scrollToElement方法
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
    }
  },
  watch: {
    // 监听数据的变化，延时refreshDelay时间后调用refresh方法重新计算，保证滚动效果正常
    item() {
      setTimeout(() => {
        this.scroll.refresh();
      }, this.refreshDelay);
    }
  }
};
</script>
<style scoped>
.wrapper-father {
  position: fixed;
  top: 100px;
  bottom: 0;
}
.scroll-content {
  height: 95%;
}
/* .wrapper {
  width: 100%;
  height: 550px;
  overflow: hidden;
  position: relative;
} */

li {
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  text-align: center;
}

/* 下拉、上拉提示信息 */
.top-tip {
  position: absolute;
  top: -40px;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #555;
}

.bottom-tip {
  width: 100%;
  height: 35px;
  line-height: 35px;
  text-align: center;
  color: #777;
  background: #f2f2f2;
  position: absolute;
  bottom: -35px;
  left: 0;
}
</style>




