<template>
  <div id="e3" style="max-width: 400px; margin: auto;" class="grey lighten-3">
    <v-card>
      <v-container fluid grid-list-lg>
        <v-layout row wrap>
          <v-card v-if="total<1">
            <v-card-title>收藏列表是空的</v-card-title>
          </v-card>
          <v-flex xs12 v-for="(item,index) in articles" :key="index" @click="getDetail(item.id)">
            <v-card>
              <v-card-title primary-title>
                <div>
                  <div class="headline">{{item.title}}</div>
                  <span>{{item.subject}}</span>
                </div>
              </v-card-title>
              <v-card-actions>
                <v-btn flat>{{item.author}}</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat @click="removeStar(item.id,index)">取消</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
        <v-layout justify-center two-line>
          <v-progress-circular v-if="loadProgress" indeterminate color="red"></v-progress-circular>
          <div v-if="!loadProgress">{{loadMore}}</div>
        </v-layout>
      </v-container>
    </v-card>
  </div>
</template>
<script>
export default {
  name: "index",
  data() {
    return {
      articles: [],
      tempArticles: [],
      YHeight: 0, //上一次滚动的高度
      pageNum: 0,
      total: 0,
      userOpenId: "",
      toasted: false,
      hasNext: false,
      loadedId: [],
      loadProgress: false
    };
  },
  computed: {
    loadMore() {
      if (this.hasNext) {
        return "more";
      }
      return "没有数据了";
    }
  },
  methods: {
    async removeStar(articleId, index) {
      let e = window.event;
      e.stopPropagation();
      if (confirm("取消收藏？")) {
        let param = {
          userOpenId: this.$store.state.loginState.user.openid,
          id: articleId,
          opt: "unStar"
        };
        let result = await this.$root.post("/app/api/article/starOpt", param);
        if (result.success) {
          this.$root.app_info("取消收藏");
          this.articles.splice(index, 1);
          this.total -= 1;
        }
      }
    },
    getDetail(param) {
      this.$router.push({ name: "detail", params: { id: param } });
    },
    async getArticleList() {
      let param = { userOpenId: this.userOpenId, pageNum: this.pageNum + 1 };
      let result = await this.$root.post("/app/api/user/starList", param);
      if (result.body) {
        this.pageNum = this.pageNum + 1;
        this.total = result.body.total;
        let list = result.body.list;
        this.hasNext = result.body.hasNextPage;
        list.forEach(element => {
          this.articles.push(element);
        });
      }
    },
    moreClick(e) {
      e.stopPropagation();
      if (this.hasNext) {
        this.getArticleList();
      } else {
        this.$root.app_info("没有数据了");
      }
    }
  },
  mounted() {
    this.articles = [];
    this.pageNum = 0;
    this.userOpenId =
      this.$route.params.userOpenId ||
      this.$store.state.loginState.user.openid ||
      3;
    this.getArticleList();
  },
  beforeRouteLeave(to, from, next) {
    if (to.name == "detail") {
      from.meta.keepAlive = true;
    }
    next();
  }
};
</script>


