<template>
  <div>
    <swiper :options="swiperOption" ref="mySwiper">
      <!-- slides -->
      <swiper-slide>
        <img data-src="http://lorempixel.com/1200/1200/nature/1" class="swiper-lazy">
        <div class="swiper-lazy-preloader"></div>
      </swiper-slide>
      <swiper-slide>
        <img data-src="http://lorempixel.com/1200/1200/nature/1" class="swiper-lazy">
        <div class="swiper-lazy-preloader"></div>
      </swiper-slide>
      <swiper-slide>
        <img data-src="http://lorempixel.com/1200/1200/nature/1" class="swiper-lazy">
        <div class="swiper-lazy-preloader"></div>
      </swiper-slide>
      <!-- Optional controls -->
      <div class="swiper-pagination" slot="pagination"></div>
      <div class="swiper-button-prev" slot="button-prev"></div>
      <div class="swiper-button-next" slot="button-next"></div>
    </swiper>
  </div>
</template>
<script>
import "swiper/dist/css/swiper.css";

import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      swiperOption: {
        // autoHeight: true,
        mousewheel: true,
        observer: true, //动态加载数据时建议使用
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        pagination: {
          el: ".swiper-pagination"
        },
        on: {
          slideChangeTransitionEnd: () => {
            let sliderNum = this.swiper.slides.length; //一共多少个
            let activeIndex = this.swiper.activeIndex; //当前滑动到第几个，从0开始
          }
        },
        lazy: {
          loadPrevNext: true
        }
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {}
};
</script>
<style scoped>
.swiper-container {
  width: 100%;
  height: auto;
  top: 10px
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
}
.swiper-container .swiper-slide {
  height: 400px;
}
img {
  max-width: 100%;
}
.mywrapper{
  width:100%;height:100%;background-color: rgb(138, 151, 163);
}
</style>



